<template>
  <div class="registration-layout">
    <el-tabs
      v-model="activeName"
      type="card"
      class="demo-tabs"
      @tab-click="handleClick"
    >
      <el-tab-pane :label="by_phone" name="first">
        <el-input v-model="inputphone" placeholder="">
          <template #prepend>{{ phone }}</template>
        </el-input></el-tab-pane
      >
      <el-tab-pane :label="by_email" name="second">
        <el-input v-model="inputemail" placeholder="">
          <template #prepend>{{ email }}</template>
        </el-input>
      </el-tab-pane>
    </el-tabs>

    <el-select
      v-model="value"
      placeholder="Select"
      @change="changeCountry(value)"
    >
      <el-option
        v-for="item in arrOfObj1"
        :key="item.name"
        :label="item.name"
        :value="item.name"
        :disabled="item.disabled"
      />
    </el-select>

    <el-select v-model="currency" placeholder="Select">
      <el-option
        v-for="item in currencyArr"
        :key="item"
        :label="item"
        :value="item"
      />
    </el-select>

    <el-input v-model="promocode1" placeholder="">
      <template #prepend>{{ promocode }}</template>
    </el-input>
    <div></div>
    <el-button @click="recaptcha">Execute recaptcha</el-button>
    <el-button @click="registrationHandler">{{ registration }}</el-button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      by_phone: "By phone",
      by_email: "By email",
      email: "E-Mail",
      phone: "Phone",
      promocode: "Promocode",
      registration: "Register",
      formTitle: "123",
      items: [],
      arrOfObj1: [],
      value: "",
      selected: "",
      promocode1: "",
      activeName: "first",
      countryCode: window.serverData.visitorData.countryCode,
      currencyArr: ["USD", "EUR"],
      currency: "",
      currencyCode: window.serverData.visitorData.currencyCode,
      promoApiBaseUrl: window.serverData.promoApiBaseUrl,
      inputphone: "",
      inputemail: "",
      message: "",
      registration_data: [],
      bonus_type: "casino",
    };
  },
  provide: {
    recapcha_resp: "",
  },

  async created() {
    try {
      const res = await axios.get(
        this.promoApiBaseUrl + `/landing-registration/countries`
      );
      this.items = res.data.payload;
      this.arrOfObj1 = Object.values(this.items);

      let code = window.serverData.visitorData.countryCode;
      let obj = Object.keys(this.items);
      for (let i = 0; i < obj.length; i++) {
        if (obj[i] == code) {
          let index = i;
          this.inputphone = "+" + this.arrOfObj1[index].phone_code;
          this.value = this.arrOfObj1[index].name;
          console.log(this.inputphone);
        }
      }
    } catch (error) {
      console.log(error);
    }
  },

  components: {},

  methods: {
    handleClick(event) {
      console.log(event);
    },
    async getCapchaKey() {
      try {
        const res = await axios.get(this.promoApiBaseUrl + `/recaptcha-key/v3`);

        this.site_key = res.data.payload.key;
        console.log(this.site_key);
      } catch (error) {
        console.log(error);
      }
    },
    loadComplete() {
      this.currencyArr.unshift(this.currencyCode);
      this.value = this.inputphone;
      this.currency = this.currencyCode;
    },
    changeCountry(value) {
      let obj = this.arrOfObj1;
      for (let i = 0; i < obj.length; i++) {
        if (obj[i].name == value) {
          let index = i;
          this.inputphone = "+" + this.arrOfObj1[index].phone_code;
          this.value = this.arrOfObj1[index].name;
        }
      }
    },
    async recaptcha() {
      // (optional) Wait until recaptcha has been loaded.
      await this.$recaptchaLoaded();

      // Execute reCAPTCHA with action "login".
      const token = await this.$recaptcha("login");
      this.recapcha_resp = token;
      // Do stuff with the received token.
    },
    registrationHandler() {
      console.group(123);
      this.registration_data.push('site_id:' + window.serverData.siteId);
      this.registration_data.push('phone:' + this.inputphone);
      this.registration_data.push('recaptcha_response:' + this.recapcha_resp);
      this.registration_data.push('currency_code:' + this.currency);
      this.registration_data.push(
        'country_code:' + window.serverData.visitorData.countryCode
      );
      this.registration_data.push('btag:' + window.serverData.btag);
      this.registration_data.push('promo_code:' + this.promocode1);
      this.registration_data.push('bonus_type:' + this.bonus_type);
      const jsonStrings = this.registration_data.map(item => JSON.stringify(item))
      console.log(jsonStrings);
      alert( jsonStrings );
      console.groupEnd(123);
      this.registration_data = [];
    },
  },
  mounted() {
    this.loadComplete();
    this.getCapchaKey();
  },
};
</script>

<style>
body {
  background: rgb(95, 95, 95);
}
.registration-layout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  width: 100%;
  position: relative;
  margin: 0 auto;
  max-width: 450px;
  color: #000;
  background: white;
  padding: 40px;
}
</style>
